@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@800&family=Josefin+Sans:wght@100&family=Nova+Round&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:700);
body {
  margin: 0;
 
}



.notFound{background:#63a4e9;border-radius:25px;position:absolute;max-height:100%;width:100%;text-align:center}.notFound h3{position:absolute;left:45%;bottom:40%;margin-bottom:8rem;font-size:2rem}

* {
  padding: 0;
  border: none;
  outline: none;
}
h2 {
  font-size: 2.5rem;
  padding: 1.5rem 0 4rem;
  color: rgb(99, 164, 233);
  text-shadow: 1px 4px 1px black;
}
.contact-form {
  margin: 3rem auto 0;
  max-width: 80%;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border: 1px solid #18dcff;
  border-radius: 25px;
  background: #2a4142;
  padding: 0 30px 10px;
  box-shadow: 0px 4px 10px rgba(51, 51, 51, 0.637);
}
.form-content {
  display: grid;
  grid-template-rows: repeat(4, 1fr) 105px;
}
input,
textarea {
  background: none;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 1.6rem;
  border-bottom: 1px solid#cec950;
  color: #ffffff;
}
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
  color: rgb(190, 188, 188);
}
input::placeholder,
textarea::placeholder {
  color: rgb(190, 188, 188);
}
input,
textarea {
  width: 90%;
  margin: 0 auto;
}
input {
  padding: 24px 0 12px;
}
.button {
  font-size: 1.3rem;
  margin-top: 7px;
  color: #cec950;
  border: none;
  cursor: pointer;
  transition: 0.2s;
  width: 100%;
}
.button:hover {
  letter-spacing: 2px;
}
.form-message {
  margin-top: 10px;
  padding: 12px;
  opacity: 0;
  transition: 0.2s ease;
  color: black;
  border-radius: 4px;
  box-shadow: 0 0 2px rgba(51, 51, 51, 0.3);
}
.errorMessage {
  color: #ff4d4d;
  padding-left: 4.5%;
}
@media screen and (max-width: 800px) {
  h2 {
    font-size: 1.5rem;
  }
  .contact-form {
    margin: 3rem auto 0;
    max-width: 99%;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    border: 1px solid #18dcff;
    background: #2a4142;
    padding: 0 5px 10px;
    box-shadow: 0px 4px 10px rgba(51, 51, 51, 0.637);
  }
}
@media screen and (max-height: 600px) {
  .contacts {
    height: 200vh;
  }
}

.contraint {
  display: flex;
  background-image: url(/Attendance_Management/static/media/fond_.2055484e.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  position: absolute;
  width: 100%;
  height: 100%;
}

